.loaders-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-gap: 1em;
  grid-auto-rows: 140px;
  background: #33333340;
  box-shadow: 0 8px 32px 0 #1f26875e;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid #ffffff2e;
  align-items: center;
}
.loaders-container > div {
  justify-self: center;
}
.loader-body:hover {
  cursor: pointer;
}
.source-text {
  visibility: hidden;
}
.loader-body:hover > .source-text {
  visibility: visible;
}

.css-container {
  padding: 5px;
  height: 70%;
  width: 100%;
  overflow: auto;
  display: flex;
  justify-content: space-between;
}

.modal-body {
  height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.modal-loader {
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  padding: 16px 0px;
  margin-bottom: 2em;
}
